import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
import { navigate } from "gatsby";

const CarpetOfferRedirect: React.FC<PageProps> = () => {
  React.useEffect(() => {
    // Client-side redirect
    navigate("/carpet-installation-boise-a", { replace: true });
  }, []);

  return (
    <main style={{ padding: "4rem", textAlign: "center" }}>
      <h1>Redirectingâ€¦</h1>
      <p>
        If you are not redirected automatically,{" "}
        <a href="/carpet-installation-boise-a">click here</a>.
      </p>
    </main>
  );
};

export default CarpetOfferRedirect;

export const Head: HeadFC = () => {
  return (
    <>
      {/* SEO-friendly 301 signal for crawlers */}
      <meta httpEquiv="refresh" content="0;url=/carpet-installation-boise-a" />
      <title>Redirecting to Carpet Installation Boise A | Ideal Floors</title>
    </>
  );
};
